@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption&display=swap);
.playground{background-color:#61dafb;margin:0 auto;padding:2px}.playground div{display:contents}.playground .rowCells{display:-webkit-flex;display:flex}.playground .cell{position:relative;width:100%;background-color:gray;border-radius:5px;margin:2px}.playground .cell:before{content:" ";display:block;width:100%;padding-top:100%}.playground .cell .cellImg{position:absolute;top:0;left:0;bottom:0;right:0;width:inherit;height:auto;-webkit-animation:show-btnImg 1s forwards;animation:show-btnImg 1s forwards}.btnStart{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding:0 5px}.btnStart button{border-radius:10px;font-size:30px;border-color:#069cc6;-webkit-animation:show-btnImg 1s forwards;animation:show-btnImg 1s forwards;background-color:#07b0df;padding:0 3px}.btnStart button:hover{background-color:#069cc6}@-webkit-keyframes show-btnImg{0%{opacity:0}100%{opacity:1}}@keyframes show-btnImg{0%{opacity:0}100%{opacity:1}}@media screen and (max-width: 500px){.btnStart button{font-size:20px}}
.title{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;font-weight:500;font-size:40px;height:60px}nav{background-color:#069cc6;color:#f0f8ff}nav :first-child{display:-webkit-flex;display:flex}nav .li_margin{margin-right:20px}nav .nav_ul{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-style:normal;font-weight:400;font-size:30px;padding:5px 10px;cursor:default}nav li{padding:5px 10px;border:1px solid #61dafb;border-radius:10px}nav li:hover{background-color:#07b0df}@media screen and (max-width: 500px)and (min-width: 421px){nav .nav_ul{font-weight:350;font-size:25px;padding:4px 8px}}@media screen and (max-width: 420px)and (min-width: 360px){nav .nav_ul{font-weight:300;font-size:20px;padding:3px 6px}}@media screen and (max-width: 359px){nav .nav_ul{font-weight:260;font-size:17px;padding:2px 4px}}
.track{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.track .arrow{height:100px;width:auto;background-color:#61dafb}.track .homer{width:auto;height:100px;-webkit-animation:show-result 1s forwards;animation:show-result 1s forwards}.track .result{height:100px;width:auto;-webkit-animation:show-result 1s forwards;animation:show-result 1s forwards}@-webkit-keyframes show-result{0%{opacity:0}100%{opacity:1}}@keyframes show-result{0%{opacity:0}100%{opacity:1}}
.modal{height:100vh;width:100vw;background-color:rgba(0,0,0,.4);position:fixed;top:0;left:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;opacity:0;pointer-events:none}.modal .modal_content{padding:20px;border-radius:12px;background-color:#fff;border:3px solid #000}.modal .modal_content h2{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-bottom:10px;font-size:xx-large}.modal .modal_content li{margin-bottom:5px}.modal .modal_content button{display:-webkit-flex;display:flex;margin:0 auto}.activeModal{opacity:1;pointer-events:auto}.select{display:-webkit-flex;display:flex;margin:20px}.select p{margin-right:10px;font-size:x-large}select{font-size:larger;height:-webkit-max-content;height:max-content}#deleteStatistic{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;cursor:default;color:#069cc6;margin-bottom:10px}#deleteStatistic:hover{color:#07b0df;-webkit-text-decoration:underline #07b0df;text-decoration:underline #07b0df}.btnSettings{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:""}table{border-collapse:collapse;border-spacing:0}button{padding:0}
body{background-color:#9ce7fc;font-family:"PT Sans Caption",sans-serif}.wrapper{max-width:980px;margin:0 auto;background-color:#61dafb;height:100vh}
